<template>
  <div class="container text-center">
    <div class="row">
      <div class="bg-white m-5 mx-auto p-4 w-auto shadow rounded login-frm">
        <!-- Tabs Titles -->

        <!-- Icon -->
        <div class="fadeIn first">
          <img class="mb-2" style="width: 30px" src="../assets/logo.svg" />
          <h6 class="mb-4 fs-3 fw-bold text-center">ورود به حساب</h6>
        </div>
        <div
          v-if="error"
          class="bg-danger fw-bold mb-4 px-4 py-2 rounded text-white"
        >
          {{ error }}
        </div>
        <!-- Login Form -->
        <form>
          <div class="mb-3 text-start">
            <label for="login" class="form-label">تلفن همراه</label>
            <input
              type="email"
              class="form-control rounded-0"
              id="login"
              placeholder="0912*******"
              tabindex="1"
              v-model="email"
              v-on:keyup="focusOnPassword"
            />
          </div>

          <div class="mb-3 text-start">
            <label for="password" class="form-label">رمز عبور</label>

            <div class="input-group" lang="en" dir="rtl">
              <span class="input-group-btn">
                <button
                  class="btn btn-secondary rounded-0"
                  type="button"
                  tabindex="3"
                  @click="togglePass"
                >
                  <span v-show="showEyeSlash === true">
                    <i class="fas fa-eye-slash"></i>
                  </span>
                  <span v-show="showEyeSlash === false">
                    <i class="fas fa-eye"></i>
                  </span>
                </button>
              </span>
              <input
                type="password"
                id="password"
                class="form-control"
                placeholder="*******"
                dir="ltr"
                tabindex="2"
                v-model="password"
                @keypress="inputAuth"
              />
            </div>
          </div>

          <div class="mb-3 mt-5 text-end">
            <button
              type="button"
              class="btn btn-theme w-100 fw-bold rounded-0"
              tabindex="4"
              @click="auth"
            >
              {{ login_text }}
            </button>
          </div>
        </form>

        <!-- Remind Passowrd -->
        <div id="formFooter" class="d-flex justify-content-between mb-3 mt-5">
          <router-link
            to="/forgetpassword"
            class="text-decoration-underline text-reset"
          >
            فراموشی رمز عبور
          </router-link>

          <router-link to="/register" class="text-decoration-none text-warning">
            رایگان ثبت نام کنید
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ArefApi from "../libs/api.js";
import utils from "../libs/utils";

export default {
  name: "login",
  created() {
    this.utils = new utils();
  },
  data() {
    return {
      showEyeSlash: true,
      email: null,
      password: null,
      error: null,
      login_text: "ورود",
      pathAfterLogin: this.$route.params.pathAfterLogin ? this.$route.params.pathAfterLogin:null
    };
  },
  methods: {
    togglePass: function () {
      this.showEyeSlash = !this.showEyeSlash;
      document.querySelector("#password").type = this.showEyeSlash
        ? "password"
        : "text";
    },
    focusOnPassword: function (e) {
      if (e.keyCode === 13) {
        document.querySelector("#password").focus();
      }
    },
    inputAuth: function (e) {
      if (e.keyCode === 13) {
        this.auth();
      }
    },
    auth: function () {
      this.error = null;
      if (!this.validateLoginFrm()) {
        return;
      }
      this.login_text = "لطفا کمی صبر کنید ...";
      let loginData = {
        email: this.email,
        password: this.password,
      };
      const arefApi = new ArefApi(this);
      arefApi
        .login(loginData)
        .then((response) => {
          this.$store.commit("set", ["token", response.data.access_token]);
          this.$store.commit("set", ["menus", response.data.menus]);
          this.$store.commit("set", [
            "menuActiveId",
            response.data.menus[0].id,
          ]);
          this.$store.commit("set", [
            "userGroupType",
            response.data.group.type,
          ]);
          this.$store.commit("set", [
            "fullName",
            `${response.data.first_name} ${response.data.last_name}`,
          ]);
          let routeName = "Home";

          switch (response.data.group.type) {
            case "admin":
              routeName = "AdminHome";
              break;

            default:
              arefApi
                .getCart()
                .then((resp) => {
                  this.$store.commit("set", [
                    "cartCount",
                    this.utils.getCartCount(resp.data)
                  ]);
                  this.$store.commit("set", ["cartAmount", resp.data.amount]);
                })
                .catch((err) => {
                  this.utils.showErrors(this, err);
                });
              break;
          }
          if(!this.$store.state.pathAfterLogin || this.$store.state.pathAfterLogin=="/login"){
            this.$router.push({ name: routeName });
          }
          else{
            const tmpPath = this.$store.state.pathAfterLogin;
            this.$store.commit("set",["pathAfterLogin",null]);
            this.$router.push({ path: tmpPath });
          }
        })
        .catch(() => {
          this.error = "نام کاربری یا رمز عبور اشتباه است";
          this.login_text = "ورود";
        });
    },
    validateLoginFrm: function () {
      if (!this.email) {
        this.error = "لطفا نام کاربری را وارد فرمایید";
        return false;
      }
      if (this.email.length != 11) {
        this.error = "لطفا نام کاربری را صحیح وارد فرمایید";
        return false;
      }
      if (!this.password) {
        this.error = "لطفا رمز عبور را وارد فرمایید";
        return false;
      }
      return true;
    },
    checkIsLogged: function () {
      if (this.$store.state.token) {
        this.$router.push({ name: "AdminHome" });
      }
    },
  },
  mounted() {
    this.checkIsLogged();
    document.querySelector("#login").focus();
  },
};
</script>
<style scoped>
.login-frm {
  min-width: 300px;
}
</style>